import React from 'react';

const EqItem=(props)=>{
return(
    <div className="it" onMouseEnter={() => props.itemShow && props.itemShow(props.idx)} onMouseLeave={() => props.itemHide && props.itemHide()}
    >
        <span className="count">{props.count}</span>
        {props.source && <img alt={props.name} src={props.source}></img>}
    </div>
)
};
export default EqItem;


