import React from 'react';
import './bottomshow.css';



const BottomShow = (props)=>{
    return(
        <div className="itemBottom">
            <p>{props.items[props.item].name}</p>
            <div className="itemImg">
                <div>
                    {props.items[props.item].features.map((element , index)=> <p key={index}>{element[0]} <span className="bold"> +{element[1]}</span></p>)}
                </div>
                <img src={props.items[props.item].img} alt={props.items[props.item].name}/>
            </div>
            <div className="coinValue">
                <div>Wartość:</div>
                <div className="v">{props.items[props.item].value}</div>
            </div>
        </div>
    )
}

export default BottomShow;