import React from 'react';
import './App.css';
import KeyboardEventHandler from 'react-keyboard-event-handler';
import {Howl} from 'howler';
import chest0 from './img/Chest1.png';
import chest1 from './img/Chest2.png';
import chest2 from './img/Chest3.png';
import success from './sound/PICKLOCK_SUCCESS.mp3';
import failure from './sound/PICKLOCK_FAILURE.mp3';
import open from './sound/OPEN_CHEST.mp3';
import coin from './img/items/gold.png';
import meat from './img/items/meat.png';
import cookedmeat from './img/items/cooked_meat.png';
import hp from  './img/items/PotionLife1.png';
import Leftstats from './components/leftstats.js';
import Eq from './components/eq.js';
import BottomShow from './components/bottomshow.js';

let items = [
    {id:0,name: 'Złoto',img: coin,features: [],value: 1},
    {id:1,name: 'Surowe Mięso',img: meat,features: [["Premia punktów trafień",6]],value: 3},
    {id:2,name: 'Smażone Mięso',img: cookedmeat,features: [["Premia punktów trafień",12]],value: 6},
    {id:3,name: 'Esencja Lecznicza',img: hp,features: [["Premia punktów trafień",50]],value: 50},
];

class App extends React.Component {

state ={
  comb: [],
  pos: 0,
  CaseAnimation: false,
  TextAnimation: false,
  prevMove: "",
  prevLock: "",
  itemShow: -1,
  eqOpen: true,
  equip: [],
  whichChest: chest0
}
lockState(){
  if(this.state.prevLock === "success") return 'Brzmi nieźle';
  else if(this.state.prevLock === "failure") return 'Cholera... Spróbuj ponownie';
  else return null;
}
checkWin(){
  if((this.state.pos === this.state.comb.length) && (this.state.pos > 0)){
  let sound = new Howl({src: open});
  sound.play();
  this.whichChest();
  this.addItems();
  this.setComb();
}
}
addItems(){
  this.setState(state=>{
    console.log("ile");
    for(let i=0; i<Math.ceil(Math.random()*2); i++){ //1-3
      let element = Math.floor(Math.random()*items.length);     // 0-3 element
      let count = Math.ceil(Math.random()*1);        // 1-2 ilość
      let item = state.equip.find(it => it.id === element);

     if(item === undefined) state.equip.push({id: element,count: count});
     else item.count += count;
    }
    state.equip.sort((a,b)=>(a.id - b.id));
    return state;
  });
}
setComb(){
  let combCount = Math.round(Math.random()*5)+4;
  let comb = [];
  for(let i=0; i<combCount; i++) comb.push(Math.floor(Math.random()*2)? "left":"right");
  this.setState({
    pos: 0,
    prevMove: '',
    prevLock: '',
    comb
  });
}
handleKeyDown(e){
  let {comb,pos} = this.state;
    if(comb[pos]===e){
      let sound = new Howl({src: success});
      sound.play();
      this.setState((prevState) => ({
        pos: prevState.pos + 1,
        prevMove: e,
        prevLock: "success",
        TextAnimation: true,
        CaseAnimation: true
      }));
    }else{
      let sound = new Howl({src: failure});
      sound.play();
      this.setState({
        pos: 0,
        prevMove: e,
        prevLock: "failure",
        TextAnimation: true,
        CaseAnimation: true
      });
    }
  }
  whichChest(){
    let ch = Math.floor(Math.random()*3);
    if(ch===0) this.setState({whichChest: chest0});
    else if(ch===1) this.setState({whichChest: chest1});
    else if(ch===2) this.setState({whichChest: chest2});
    else this.setState({whichChest: chest0});
  }
  showItem=(a)=>{
    this.setState({
      itemShow: a,
    });
  }
  hideItem=()=>{
    this.setState({
      itemShow: -1,
    });
  }
  componentDidMount(){
    this.setComb();
    this.whichChest();
  }
  componentDidUpdate(){
    this.checkWin();
  }
  render(){
    let {prevLock,prevMove,CaseAnimation,TextAnimation} = this.state;
    return (
      <div className="App c">
        <KeyboardEventHandler handleKeys={['left', 'right']} onKeyEvent={(key, e) => this.handleKeyDown(key)} />
        <Leftstats/>
        <div
          onAnimationEnd={() => this.setState({ TextAnimation: false })}
          className={TextAnimation ? `statusA status status_${prevLock}` : `status status_${prevLock}`}>{this.lockState()}
        </div>
        <img
          alt="Skrzynia"
          src={this.state.whichChest}
          onAnimationEnd={() => this.setState({ CaseAnimation: false })}
          className={CaseAnimation ? `anm_${prevMove} chest` : 'chest'}
        />
        <Eq equip={this.state.equip} items={items} itemShow={this.showItem} itemHide={this.hideItem}/>
        {(this.state.itemShow > -1) && <BottomShow item={this.state.itemShow} items={items}/>}
      </div>
    );
  }
}

export default App;
