import React from 'react';
import './eq.css';
import EqItem from './eqitem.js';


const Eq = (props)=>{
      let eqlist = props.equip.map((item) =>(
        <EqItem idx={item.id} key={item.id} source={props.items[item.id].img} count={item.count} name={item.name} itemShow={props.itemShow} itemHide={props.itemHide}/>
      ));
      if(eqlist.length%5){
          let sup = 5 - eqlist.length%5;
        for(let i=0; i< sup;i++) eqlist.push(<EqItem key={eqlist.length}/>);
      }
      while(eqlist.length < 30) eqlist.push(<EqItem key={eqlist.length}/>);

        return (
            <div className="c equ">
                {eqlist}
            </div>
        )
}

export default Eq;